*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

.heading
{
	margin-top: 50px;
	margin-bottom: 20px;
}

.ml-50
{
	margin-left: 50px;
}

.card
{
	width: 200px;
	height: 200px;
	border: 1px solid black;
	margin-top: 100px;
	margin-left: 5%;
	display: inline-block;
	cursor: pointer;
}

.card img
{
	width: 100%;
	height: 100%;
}

.startbtn
{
	width: 100px;
	height: 40px;
	color: white;
	background: maroon;
	border-radius: 5px;
	border:none;
	cursor: pointer;
}

.gameover
{
	margin-top: 50px;
	font-size: 80px;
	color: #333;
	
}